/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

@-webkit-keyframes redNeon {
  0% {
    text-shadow: 0 0 0 #ff0008; }
  50% {
    text-shadow: 0 0 2px #ff0008, 0 0 1px #F8F9E8; }
  100% {
    text-shadow: 0 0 2px #BD3649, 0 0 1px #F8F9E8; } }

@keyframes redNeon {
  0% {
    text-shadow: 0 0 0 #ff0008; }
  50% {
    text-shadow: 0 0 2px #ff0008, 0 0 1px #F8F9E8; }
  100% {
    text-shadow: 0 0 2px #BD3649, 0 0 1px #F8F9E8; } }

@-webkit-keyframes whiteNeon {
  0% {
    text-shadow: 0 0 0 white; }
  100% {
    text-shadow: 0 0 22px white; } }

@keyframes whiteNeon {
  0% {
    text-shadow: 0 0 0 white; }
  100% {
    text-shadow: 0 0 22px white; } }

@-webkit-keyframes stopBlink {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes stopBlink {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@-webkit-keyframes blink {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes blink {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@-webkit-keyframes curtainIn {
  0% {
    height: 300px; }
  100% {
    height: 0px; } }

@keyframes curtainIn {
  0% {
    height: 300px; }
  100% {
    height: 0px; } }

@-webkit-keyframes curtainOut {
  0% {
    height: 0px; }
  100% {
    height: 300px; } }

@keyframes curtainOut {
  0% {
    height: 0px; }
  100% {
    height: 300px; } }

body {
  font: 15px 'Bubbler One', sans-serif;
  background: #1f1f45; }

.curtainIn {
  -webkit-animation: curtainIn .1s linear forwards;
          animation: curtainIn .1s linear forwards; }

.curtainOut {
  -webkit-animation: curtainOut .5s linear forwards;
          animation: curtainOut .5s linear forwards; }

.block {
  visibility: visible !important;
  opacity: 1 !important;
  -webkit-transition: visibility opacity 4s ease;
  -o-transition: visibility opacity 4s ease;
  transition: visibility opacity 4s ease; }

.hide {
  visibility: hidden !important;
  opacity: 0 !important;
  -webkit-transition: visibility opacity 1s ease;
  -o-transition: visibility opacity 1s ease;
  transition: visibility opacity 1s ease; }



